* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
  border: none;
  outline: none;
  font-family: var(--font-family-reg);
}

button {
  cursor: pointer;
}

body {
  background: var(--color-white);
  overflow-x: hidden;
}

a {
  color: unset;
  text-decoration: none;
  font-family: var(--font-family-reg);
}
p{
  text-align: justify;
}

.row {
  display: flex;
  flex-direction: row;
  gap: 1.5rem;
}

.section__padding {
  padding: 1rem 6rem;
  padding-right: 0rem;
}

.section__margin {
  margin: 1rem 6rem;
}

.secondary__button {
  background-color: var(--secondary-color);
  padding: .6rem;
  border-radius: .2rem;
  color: var(--text-color);
}

.secondary__button:hover {
  background-color: #640750;
}

@media screen and (max-width: 1100px) {
  .section__padding {
    padding-left: 4rem;
    padding-right: 0rem;
  }

  .section__margin {
    margin: 1rem 4rem;
  }
}

@media screen and (max-width: 550px) {
  .section__padding {
    padding: 1rem 2rem;
    padding-right: 0rem;
  }

  .section__margin {
    margin: 1rem 2rem;
  }
}

.scale-up-center {
  -webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
  animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

/* CSS for the fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    /* Completely transparent */
  }

  to {
    opacity: 1;
    /* Fully visible */
  }
}

/* Apply the animation to an element */
.fade-in {
  animation-name: fadeIn;
  /* Use the fadeIn keyframes animation */
  animation-duration: 1s;
  /* Animation duration (adjust as needed) */
  animation-timing-function: ease-in;
  /* Timing function (adjust as needed) */
  animation-fill-mode: forwards;
  /* Keep the final state after animation */
}