.testo{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin: 2rem 5rem;
}
.testo-row{
    display: flex;
    flex-direction: row;
    gap: 2rem;
    justify-content: space-between;
    margin-right: 2rem;
}
.single-testo{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 300px;
}

@media screen and (max-width: 500px) {
    .testo-row{
        flex-direction: column;
    }
}