.about-section {
    color: var(--neutral-color);
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 3rem;
}

.about-section>h1 {
    text-align: center;
    margin-bottom: 5rem;
}



.about-section-content {
    display: flex;
    flex-direction: row;
}

.about-section-content img {
    height: 50rem;
    width: 50%;
    position: sticky;
    top: 0;
}

.about-content-detail {
    display: flex;
    flex-direction: column;
    padding-right: 3rem;
    padding-top: 5rem;
    gap: 2rem;
    height: 70rem;

}

.about-content-detail h1 {
    text-decoration: double underline;
}

.about-content-detail .about-detail-point p {
    display: flex;
    align-items: center;
}

.about{
    display: flex;
    flex-direction: column;
    padding-top: 6rem;
    padding-bottom: 6rem;
    padding-right: 6rem;
    background-color: #F3F3F3;
    gap: 5rem;
}
.about h1{
    text-align: center;
    font-size: 46px;
}

@media screen and (max-width: 550px) {
    .about-section-content {
        flex-direction: column-reverse !important;
    }

    .about-section-content img {
        width: 100%;
        height: 20rem;
    }

    .about-content-detail {
        width: 100%;
        height: 30rem;
    }
}