.navbar {
    display: flex;
    flex-direction: row;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    justify-content: space-between;
    position: sticky;
    top: 0;
    background-color: var(--text-color);
    z-index: 1000;
}

.navbar img {
    width: 100px;
    cursor: pointer;
}

.navbar img:hover {
    border: 1px solid var(--primary-color);
}

.navbar .nav-item {
    display: flex;
    padding-right: 1rem;
}

.navbar ul {
    display: flex;
    list-style: none;
    gap: 2rem;
    padding-right: 2rem;
    align-items: center;
}

.navbar ul li {
    cursor: pointer;
    color: var(--accent-color);
    font-size: 18px;
}

.navbar ul li:hover {
    color: var(--accent-color);
    font-weight: 700;
}

.navbar-menu {
    margin: 0rem 1rem;
    display: none;
    position: relative;
}

.navbar-menu svg {
    cursor: pointer;
}

.navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--text-color);
    padding: 2rem;
    position: absolute;
    right: 0;
    margin-top: 2.5rem;
    width: 50rem;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}

.navbar-menu_container ul {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

@media screen and (max-width: 1024px) {
    .navbar-menu {
        display: flex;
    }

    .navbar #nav-link {
        display: none;
    }
}

@media screen and (max-width: 500px) {
.navbar-menu_container{
    width: 20rem;
}
}