.feature {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.sub-navbar {
    display: flex;
    flex-direction: row;
    width: 100%;
    padding-left: 2rem;
    border-bottom: 1px solid var(--neutral-color);
}

.sub-navbar ul {
    display: flex;
    list-style: none;
    width: 50%;
    padding: 1rem;
    padding-bottom: 0;
}

.sub-navbar ul li {
    flex-grow: 1;
    cursor: pointer;
    font-size: 2.4rem;
    position: relative;
    text-align: center;
    transition: background-color 0.3s ease-in-out;
    color: #909090;
}

.sub-navbar ul li::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: var(--neutral-color);
    transform: scaleX(0);
    transform-origin: center;
    transition: transform 0.3s ease-in-out;
}

.sub-navbar ul li:hover::before {
    transform: scaleX(1);
}

.sub-navbar ul li.clicked {
    background-color: rgba(0, 0, 0, 0.2);
    transition: background-color 0.3s ease-out;
    color: var(--neutral-color);
    border-bottom: 2px solid var(--neutral-color);
}

.feature-content {
    display: flex;
}

.feature-section-content {
    display: flex;
    padding-top: 2rem;
    gap: 1rem;
}

.feature-section-content img {
    width: 52%;
}

.feature-content-detail {
    display: flex;
    flex-direction: column;
    width: 48%;
    padding: 5rem;
}


@media screen and (max-width: 1024px) {
    .sub-navbar {
        padding-left: 0;
    }

    .sub-navbar ul {
        width: 100%;
        padding-left: 0;
    }

    .sub-navbar ul li {
        font-size: 20px;
    }
}

@media screen and (max-width: 500px) {
    .feature-section-content {
        flex-direction: column-reverse;
    }

    .feature-section-content img {
        width: 100%;
    }

    .feature-content-detail {
        width: 100%;
    }
}