.bench-mark {
    display: flex;
    flex-direction: column;
    color: var(--neutral-color);
    margin-top: 3rem;
}

.bench-mark-content {
    margin-top: 5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-right: 5rem;
}

.bench-mark-content img{
    width: 40%;
    height: 570px;
}
.bench-mark-detail{
    display: flex;
    flex-direction: column;
    background-color: #D9D9D9;
    width: 50%;
    padding: 2rem;
    padding-left: 3rem;
}
.bench-mark-detail > ul{
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.bench-mark-detail .sub-list{
    list-style: disc;
    padding-left: 1rem;
}

@media screen and (max-width: 500px) {
    .bench-mark-content{
        flex-direction: column;
        padding-right: 1rem;
    }
    .bench-mark-content img{
        width: 100%;
        height: 300px;
    }
    .bench-mark-detail{
        width: 100%;
    }
}