footer{
    display: flex;
    flex-direction: column;
    background-color: #F3F3F3;
}
.footer{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    padding-top: 5rem;
    padding-right: 6rem;
    gap: 2rem;
    justify-content: space-between;
}
.footer-col-1{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    width: 25%;
}
.footer-col-1 img{
    width: 10rem;
}
.footer-col-2{
    display: flex;
    flex-direction: 
    column;
}
.footer h3{
    border-bottom: 2px solid #94A3B8;
    width: fit-content;
    padding-bottom: .3rem;
    color: #94A3B8;
}
.footer ul{
    list-style: none;
    margin-top: 1rem;
}
.footer .footer-col-4 form{
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.footer-bottom{
    margin: 1rem 2.5rem;
    border-top: 2px solid var(--neutral-color);
    padding: 1rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    color: #999999 !important;
}
.footer-bottom .row svg{
    font-size: 24px;
    cursor: pointer ;
}
.footer-bottom .row svg:hover {
    color: var(--neutral-color);
}