.search {
    margin: auto;
    background-color: #D9D9D9;
    width: 70%;
    padding: 1rem;
    border-radius: 1rem;
    position: relative;
    top: -2.5rem;
    padding-bottom:0;
}

.search form {
    display: flex;
    gap: 5rem;
    padding: 2rem;
}
.search-icon{
    display: flex;
    width: 70%;
    justify-content: center;
    background-color: white;
    margin: auto;
    margin-bottom: 0;
    border-radius: 10px 10px 0 0;
    justify-content: space-between;
    padding: .5rem 3rem;
    text-align: center;
}
@media screen and (max-width: 500px) {
    .search-icon, .search form{
        flex-direction: column;
    }
}