.hero {
    background-image: url("../../../public/assets/hero2.JPG");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    height: 32rem;
}

.hero .hero-content {
    margin-top: 50px;
}

.hero-content p {
    color: var(--accent-color);
    font-size: 22px;
    font-weight: 600;
}

.hero-content h1 {
    color: var(--text-color);
    font-size: 40px;
}

.hero-content h3 {
    margin: 30px 0px;
    color: var(--text-color);
}

.hero-content form label {
    color: var(--text-color);
    border-bottom: 1px solid var(--text-color);
    padding: 5px;
    background-color: rgba(51, 51, 51, 0.8);
    width: 300px;
}

.hero-content form input {
    background-color: transparent;
    padding: 5px;
    font-size: 16px;
    color: var(--text-color);
}
.hero-content form input::placeholder{
    color: var(--text-color);
}

.hero-content form button {
    margin-left: 1rem;
}

.client {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    width: 80%;
    height: fit-content;
    margin: auto;
    position: relative;
    top: -2.5rem;
    background-color: var(--text-color);
    border: 1px solid var(--neutral-color);
    border-radius: .2rem;
    display: flex;
    align-items: center;
    padding: 1rem;
    justify-content: space-between;
}



.single-client {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    justify-content: center;
    gap: 2rem;
}

.carousel .slide img {
    max-width: 13rem;
    max-height: auto;
    margin-right: 1rem;
}

.carousel .slide h2 {
    font-size: 1.5rem;
    color: var(--neutral-color);
    margin: 0;
}

/* Custom Carousel CSS */
.client .carousel {
    width: 100%;
    margin: 0 auto;
}

.client .carousel-root {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
}

.client .carousel .slider-wrapper {
    overflow: hidden;
    position: relative;
}

.client .carousel .slider-wrapper .slider {
    display: flex;
    transition: transform 0.3s ease;
    align-items: center;
}

.client .carousel .slider-wrapper .slider .slide {
    width: 100%;
    max-width: 100%;
    display: flex;
    justify-content: center;
}

/* Custom styles for the arrow buttons */
.carousel .control-arrow {
    display: flex;
    padding: 0 !important;
    margin-top: 2rem !important;
    align-items: center;
    justify-content: center;
    width: 2rem;
    height: 2rem;
    background-color: var(--accent-color) !important;
    font-size: 24px;
    border-radius: 50%;
    transition: color 0.3s ease;
}



@media screen and (max-width: 1025px) {
    .hero {
        width: 100%;
        height: 400px !important;
    }

    .hero-content {
        margin-top: 1rem !important;
    }

    .hero-content h1 {
        font-size: 26px;
    }

    .hero-content h3 {
        font-size: 16px;
        margin: .3rem;
    }

    .client {
        width: 90% !important;
    }
}

@media screen and (max-width: 500px) {

    .hero-content p {
        font-size: 14px;
    }

    .hero-content h1 {
        font-size: 20px;
    }

    .hero-content h3 {
        margin-bottom: .3rem;
    }


    .hero-content form {
        display: flex;
        flex-direction: column;
        width: fit-content;
        gap: 1rem;
    }

    .hero-content form button {
        width: fit-content;
        margin-left: 0;
    }

    .client {
        top: 0;
    }

    .single-client img {
        display: none !important;
    }

    .carousel .control-arrow {
        margin-top: .5rem !important;
    }
}