.work {
    background-image: url("../../../public/assets/hero2.JPG");
    background-size: 100% 500px;
    background-repeat: no-repeat;
    background-position: top center;
    height: 800px;
    width: 100%;
}
.work h2{
    color: var(--text-color);
    padding: 5rem;
    font-size: 36px;
}
.mobile-layout {
    background-image: url("../../../public/assets/phone.png");
    background-size: contain;
    background-repeat: no-repeat;
    width: 246px;
    height: 510px;
    position: relative;
    margin: auto;
    top: 5%;
    overflow: hidden;
}

.mobile-slider {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.mobile-slider .carousel-root, .carousel-slider, .slider-wrapper, .slider {
    width: 100%;
    height: 100%;
}

.mobile-slider .carousel-root .work-slider {
    width: 100%;
    height: 100%;
}

.mobile-slider .carousel-root .work-slider img {
    position: relative;
    top: 20px;
    width: 3000px;
    height: 90%;
    z-index: -1;
}

.work-slider p{
    position: relative;
    right: -200px;
}